import * as React from 'react';
import { memo, useEffect, useState, useCallback } from 'react';
import { Link } from 'gatsby';
import { throttle } from 'lodash';

import './postList.scss';
//import { FormatTextdirectionRToL } from '@material-ui/icons';
import config from '../../../config.js';

export interface PostListProps {
  posts: any[];
  targetCategory: string;
}

const PostList = memo((props: PostListProps) => {
  const { posts, targetCategory } = props;
  const [showCnt, setShowCnt] = useState(100);

  const throttleScrollHandler = useCallback(
    throttle(() => {
      if (
        window.outerHeight > (document.querySelector('.post-list') as HTMLDivElement).getBoundingClientRect().bottom
      ) {
        setShowCnt((prev: number) => {
          if (prev >= posts.length) return prev;
          return prev + 10;
        });
      }
    }, 250),
    []
  );

  useEffect(() => {
    window.addEventListener('scroll', throttleScrollHandler);

    return () => {
      window.removeEventListener('scroll', throttleScrollHandler);
    };
  }, []);

  posts.sort((a: any, b: any) => {
    const aDate = new Date(a.node.frontmatter.update ?? a.node.frontmatter.date);
    const bDate = new Date(b.node.frontmatter.update ?? b.node.frontmatter.date);

    if (aDate < bDate) return 1;
    if (aDate > bDate) return -1;
    return 0;
  });

  let iDisplayPost=0;

  const mapPost = posts.map((post: any, i: number) => {
    const { node } = post;
    const { excerpt, fields, frontmatter } = node;
    if (fields.slug === undefined) {return (<div/>);}
    const { slug } = fields ;
    const { date, title, tags, category, titleImage } = frontmatter;
    let update = frontmatter.update;
    if (Number(update.split(',')[1]) === 1) update = null;

    const mapTag = tags.map((tag: string) => {
      if (tag === 'undefined') return;

      return (
        <li key={`${slug}-${tag}`} className="tag">
          <span>
            <Link to={`/tags#${tag}`}>{`#${tag}`}</Link>
          </span>
        </li>
      );
    });

    const getImage = (src) =>{
      let tempImg ="";
      try{
        tempImg = require('../../images/' + src);
      }catch(e){
        console.log('No image -> default image is used.!')
        tempImg = require('../../images/' + config.defaultTitleImage);
      };
      return tempImg;
    };

    const showThumbnail = () => {
      const temp = titleImage? titleImage:"/macbook.jpeg";
      const unsplashRandom = 'https://source.unsplash.com/random';
      const titleImageName = temp=='random'? unsplashRandom: getImage(temp);
      //const desc = tags.length>0 ? tags[0] : title.substr(0,10)
      const desc = slug.split('_')[0].substr(1,10);

      return(
        <div className="thumbnailImage" >
          <a href={slug}>
          <img src={titleImageName} style={{margin:0, padding:0}} />
            <div className="overlay">
            <span>[{category}]</span> <span>{desc}</span>
            </div>
          </a>
        </div>
      );
    }
    

    if(targetCategory !== undefined && category!==undefined) {
      if(targetCategory != category) {return (<div/>);}
    }

    iDisplayPost = iDisplayPost+1

    return (
      
        
      <li key={slug} className={`post ${ iDisplayPost < showCnt ? 'show' : 'hide'}`}>
        
        
        <article className="thumbnailContainer">

            <div className="thumbnailImageContainer">
              {showThumbnail()}
            </div>

            <div className="articleContainer">
              <h2 className="title">
                <Link to={slug}>{title}</Link>
              </h2>
              <div className="info">
                <div className="date-wrap">
                  <span className="date">{date}</span>
                  {update ? <span className="update">&nbsp;{`(Updated: ${update})`}</span> : null}
                </div>
                {tags.length && tags[0] !== 'undefined' ? <span className="info-dot">Â·</span> : null}
                <ul className="tag-list">{mapTag}</ul>
              </div>
              <span className="excerpt">
                <Link to={slug}>{excerpt}</Link>
              </span>
            </div>

        </article>
      </li>
     
    );
  });
  return (
    <div className="post-list">
      <ul>{mapPost}</ul>
    </div>
  );
});

export default PostList;
